*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root{
    margin: 15vh auto;
    width: 500px;
    height: 500px;
}

#data-wrapper{
    display: flex;
    margin-top: 1rem;
    justify-content: space-evenly;
}

#break-wrapper{
    background-color: #9BA4B5;
    color: #212A3E;
    width: 200px;
    height: 200px;
    text-align: center;
    border-radius: 50%;
    padding: 2rem;
    border: 7px solid #212A3E;
    box-shadow: 0px 0px 10px black;
    transform: translate(-20px, 0px);


}

#session-wrapper{
    padding: 2rem;
    background-color: #9BA4B5;
    color: #212A3E;
    width: 200px;
    height: 200px;
    text-align: center;
    border-radius: 50%;
    transform: translate(370px, -200px);
    border: 7px solid #212A3E;
    box-shadow: 0px 0px 10px black;
    

}
#session-wrapper button, #break-wrapper button{
    font-size: large;
    font-weight: bolder;
    border: 3px solid #212A3E;
    border-radius: 7px;
    width: 2rem;
}

#session-wrapper button:hover, #break-wrapper button:hover{
    background-color: aliceblue;
}

#display-wrapper{
    padding: 4rem;
    width: 350px;
    height: 350px;
    background-color: #394867;
    color: #F1F6F9;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    gap: 10px;
    border-radius: 50%;
    transform: translate(100px, -300px);
    border: 7px solid #ffffff;
    box-shadow: 0px 0px 10px black;
    

}

#display-wrapper h2{
    font-size: 34px;
}

#display-wrapper h1{
    font-size: 64px;
}

#display-wrapper button{
    padding: 0.5rem;
    border: 3px solid #F1F6F9;
    background-color: #212A3E;
    color:#F1F6F9;
    font-weight: bolder;
    border-radius: 7px;
}

#display-wrapper button:hover{
    background-color: #9BA4B5;

}